<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">const</span> lat = <span class="hljs-number">30.598026044</span>
  <span class="hljs-keyword">const</span> lng = <span class="hljs-number">114.302312702</span>
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">positions</span>: [
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">0.5</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">0.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">3</span>, <span class="hljs-attr">lat</span>: lat, <span class="hljs-attr">height</span>: <span class="hljs-number">0</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">3</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">1</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">1</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">3</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> },
          { <span class="hljs-attr">lng</span>: lng - <span class="hljs-number">0.5</span>, <span class="hljs-attr">lat</span>: lat - <span class="hljs-number">0.5</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">50000</span> }
        ],
        <span class="hljs-attr">minimumHeights</span>: [<span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>, <span class="hljs-number">0</span>]
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">wall</span>.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
        viewer.<span class="hljs-title function_">zoomTo</span>(viewer.<span class="hljs-property">entities</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
